.container {
	@media screen and (min-width: 320px) {
	  width: 480px;
	  padding-left: 20px;
	  padding-right: 20px;
	  margin: 0 auto;
	}
	@media screen and (min-width: 768px) {
	  width: 768px;
	  padding-left: 40px;
	  padding-right: 40px;
	}
	@media screen and (min-width: 1344px) {
	  width: 1344px;
	  padding-left: 15px;
	  padding-right: 15px;
	}
 }